.SideDrawer{
    background-color: white;
    position: fixed;
    height: 100%;
    width: 280px;
    max-width: 70%;
    left: 0;
    top: 0;
    z-index: 200;
    padding: 32px 16px;
    transition: transform .3s ease-out;
}

@media (min-width:500px){
    .SideDrawer{
        display: none;
    } 
}

.SideDrawerLogo{
    height: 11%;
}

.Open{
    transform: translateX(0);
}

.Close{
    transform: translateX(-100%);
}