.Toolbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #703b09;
    height: 60px;
    width: 100%;
    /* position: fixed; */
    top: 0;
    left: 0;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav{
    height: 100%;
}

.ToolBarLogo{
    height: 80%;
}

@media (max-width: 499px){
    .DesktopOnly{
        display: none;
    }
}


@media (max-width: 500px) {
    nav{
        margin-top: 25px !important;
    }
}